.header {
    text-align: center;
    color: #fff;
    padding: 20px;
  }
  
  .title {
    font-size: 2.5em;
    font-weight: bold;
    margin: 0;
    position: relative;
    overflow: hidden;
    display: inline-block;
    animation: flicker 1.5s infinite alternate;
    color: #ffe149;
    text-shadow: 
      0 0 5px rgba(255, 255, 255, 0.5),
      0 0 10px rgba(255, 255, 255, 0.5),
      0 0 20px rgba(255, 0, 255, 0.5),
      0 0 30px rgba(255, 0, 255, 0.5),
      0 0 40px rgba(255, 0, 255, 0.5),
      0 0 50px rgba(255, 0, 255, 0.5),
      0 0 75px rgba(255, 0, 255, 0.5);
  }
  
  @keyframes flicker {
    0% { opacity: 1; }
    50% { opacity: 0.8; }
    100% { opacity: 1; }
  }
  
  @keyframes neon {
    from { text-shadow: 
      0 0 5px rgba(255, 255, 255, 0.5),
      0 0 10px rgba(255, 255, 255, 0.5),
      0 0 20px rgba(255, 0, 255, 0.5),
      0 0 30px rgba(255, 0, 255, 0.5),
      0 0 40px rgba(255, 0, 255, 0.5),
      0 0 50px rgba(255, 0, 255, 0.5),
      0 0 75px rgba(255, 0, 255, 0.5); 
    }
    to { text-shadow: 
      0 0 10px rgba(255, 255, 255, 0.5),
      0 0 20px rgba(255, 0, 255, 0.5),
      0 0 30px rgba(255, 0, 255, 0.5),
      0 0 40px rgba(255, 0, 255, 0.5),
      0 0 50px rgba(255, 0, 255, 0.5),
      0 0 75px rgba(255, 0, 255, 0.5),
      0 0 100px rgba(255, 0, 255, 0.5);
    }
  }
  