.hero {
    text-align: center;
    padding: 50px 20px;
    color: #ffffff;
  }
  
  .hero h2 {
    font-size: 2em;
    margin-bottom: 20px;
  }
  
  .hero p {
    font-size: 1.8em;
    margin-top: 0;
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    animation: blink-caret 0.75s step-end infinite;
  }
  
  @keyframes blink-caret {
    from, to { border-color: transparent; }
    50% { border-color: #ffffff; }
  }